@import '../../assets/css/variable.less';
.trading-mining{
  border-top:1px solid @divider_color;
  .check{
    display: flex;
    margin-top: 18px;
    margin-left: 45px;
    div{
      width: 88px;
      height: 31px;
      background: rgba(85, 119, 253, 0.1);
      margin-left: 7px;
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: @whiteFontColor;
      cursor: pointer;
    }
    .now{
      background: #5577FD;
    }
  }
  .now-prom{
    width: 1250px;
    height: 1444px;
    margin: 0 auto;
    margin-top: 44px;
    border: 1px solid @divider_color;
    background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
    
  }
}
@media only screen  and (max-width:759px) {
  .trading-mining{
    .now-prom{
      width: 375px;
      height: 2672px;
      border: 0;
    }
  }
}
@media only screen and (min-width : 760px) and (max-width:1139px) {
  .trading-mining{
    .now-prom{
      width: 760px;
    }
  }
}
@media only screen and (min-width : 1140px) and (max-width:1270px) {
  .trading-mining{
    .now-prom{
      width: 1135px;
      border: 0;
      border-bottom: 1px solid @defaultFontColor;
    }
  }
}
